	module.exports = {"data":{"markdownRemark":{"html":"<p>I always put coffee at my office coffee maker. By the way my favorite is straight guatemala black, surely the first cup is definitely <a href=\"http://www.amazon.co.jp/gp/product/B015S5545W/ref=as_li_ss_tl?ie=UTF8&#x26;camp=247&#x26;creative=7399&#x26;creativeASIN=B015S5545W&#x26;linkCode=as2&#x26;tag=danmaq-22\">completely indispensable</a> . Apart from that, as usual I had coffee after lunch but something strange ... .... When I was watching the coffee maker, <em>how coffee is overflowing!</em> Have you put in such a way to put it in? After stopping thought for a while, I noticed the title and stopped thought for a while. After about 20 seconds, I watched the downward water server becoming visibly in a state of emptiness, then returned to me and wiped clean. 30 g of beans died. It has recently been updated only in the press release of danmaq, so I have to write down such everyday topics too.</p>","frontmatter":{"cover":null,"date":"2016-05-23T15:27:31+09:00","strDate":"2016/5/23","title":"Lost catastrophe coffee filter"}}},"pathContext":{"path":"/en/blog/2016/05/23-大惨事コーヒーフィルター付け忘れ/","langKey":"en"}}

